<template>
<div class="event">
     <h1 class="pb-3 mb-4 font-italic ">
      {{Data.title}}
   </h1>
    <div class="container">
  <div class="banner">
      <img :src= Data.image_url />
  </div>         
 <p>{{Data.description}}</p>   
   </div>
  <div class="interested">
      <div v-if='getInfo[Data.id]'>
          You're {{getInfo[Data.id]}} in this event
      </div>
<router-link to='/'><button class="btn btn-back"> Back </button></router-link>
  <button v-if='"Going"=== getInfo[Data.id]' class="btn btn-primary" @click="Going" disabled><span class="glyphicon glyphicon-ok">Going</span></button>
  <button v-else class="btn btn-primary" @click="Going"><span class="glyphicon glyphicon-ok"></span>Going</button>
    <button v-if='"Not Going"=== getInfo[Data.id]' class="btn btn-primary" @click="noGoing" disabled><span class="glyphicon glyphicon-remove"></span>Not Going</button> 
    <button v-else class="btn btn-primary" @click="noGoing"><span class="glyphicon glyphicon-remove"></span>Not Going</button>
    </div>
</div>
</template>

<script>
import eventlist from '../data/eventdata.js'
export default {
    computed:{
        getInfo()
         {
           return this.$store.getters.getInfoo;
         },
        Data()
        {
            return eventlist[this.$route.params.id-1]
        }
    },
    methods:
    {
        Going()
        {

            let eventId =this.$route.params.id.toString()
            let obj={};
            event.target.setAttribute("disabled", "disabled");
            event.target.nextSibling.disabled = false
           obj["id"]=eventId;
           obj["checkedValue"] = event.target.innerText;
           this.$store.commit('saveValue',obj)
        },
         noGoing()
        {
            let eventId =this.$route.params.id.toString()
            event.target.setAttribute("disabled", "disabled");
            event.target.previousSibling.disabled = false
           // event.target.className = "btn btn-default"
          
           let obj={};

           obj["id"]=eventId;
            obj["checkedValue"] = event.target.innerText;
           console.log(obj)
           this.$store.commit('saveValue',obj)
        }
    }


}
</script>


<style scoped>
.interested button
{
   margin: 5px;
}
.wrapper{
  background: #497dd0;
  padding:100px;
}
.banner  img
{
    width:100%;
    height:200px;
    margin-bottom: 10px;
}


.toggle_radio{
  position: relative;
  background: rgba(255,255,255,.1);
  margin: 4px auto;
  overflow: hidden;
  padding: 0 !important;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  position: relative;
  height: 26px;
  width: 318px;
}
.toggle_radio > * {
  float: left;
}
.toggle_radio input[type=radio]{
  display: none;
  /*position: fixed;*/
}
.toggle_radio label{
  font: 90%/1.618 "Source Sans Pro";
  color: rgba(255,255,255,.9);
  z-index: 0;
  display: block;
  width: 100px;
  height: 20px;
  margin: 3px 3px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  cursor: pointer;
  z-index: 1;
  /*background: rgba(0,0,0,.1);*/
  text-align: center;
  /*margin: 0 2px;*/
  /*background: blue;*/ /*make it blue*/
}
.toggle_option_slider{
  /*display: none;*/
  /*background: red;*/
  width: 100px;
  height: 20px;
  position: absolute;
  top: 3px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  -webkit-transition: all .4s ease;
  -moz-transition: all .4s ease;
  -o-transition: all .4s ease;
  -ms-transition: all .4s ease;
  transition: all .4s ease;
}

#first_toggle:checked ~ .toggle_option_slider{
  background: rgba(255,255,255,.3);
  left: 3px;
}
#second_toggle:checked ~ .toggle_option_slider{
  background: rgba(255,255,255,.3);
  left: 109px;
}
#third_toggle:checked ~ .toggle_option_slider{
  background: rgba(255,255,255,.3);
  left: 215px;
}
</style>
