import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store ({
    state:{
        clicked :{}
    },
    mutations:{
        saveValue(state,data)
        {
            state.clicked[data.id] = data.checkedValue
        }
    },
    getters:
    {
        getInfoo(state)
            {
                return state.clicked
            }
    }
})